.error {
  font-size: 15px;
  color: red;
  border-radius: 5px;
  border-color: red;
  border-style: solid;
  background-color: rgb(187, 185, 185);
  padding: 5px;
  margin: 3px;
}

.success {
  font-size: 15px;
  color: rgb(5, 61, 5);
  border-radius: 5px;
  border-color: green;
  border-style: solid;
  background-color: rgb(187, 185, 185);
  padding: 5px;
  margin: 3px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
